.new--value {
  font-family: inter;
  margin-bottom: 1.25rem;
  margin: 2.5rem 1rem;
  font-size: 2.5rem;
}

/* CSS */
.btn {
  background-color: #f8f9fa;
  border: 1px solid #f8f9fa;
  border-radius: 4px;
  color: #3c4043;
  cursor: pointer;
  font-family: arial, sans-serif;
  font-size: 1rem;
  height: 2rem;
  /* line-height: 27px; */
  min-width: 2rem;
  padding: 0 0px;
  text-align: center;

  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: pre;
}

.btn:hover {
  border-color: #dadce0;
  box-shadow: rgba(0, 0, 0, 0.1) 0 1px 1px;
  color: #202124;
}

.btn:focus {
  border-color: #4285f4;
  outline: none;
}
